<template>
  <div id="history">
    <header-nav :title="msg"></header-nav>
    <news-list
    :deleteBol="false"
    :name="2"
    :article_check="true"
    :isHistory="1"
    :list="list"
    @deleteFn="deleteFn"
    :readLineBol="true"
    :gray="false"
    v-if="list.length !== 0"></news-list>
    <div class="no-more-data" v-if="this.list.length > 0">没有更多数据了！</div>
    <div id="noList" ref="noList" v-if="this.list.length === 0">
      <stateless-head :headData="headData"></stateless-head>
      <news-list :list="recommendList" @deleteAd="deteteAd"></news-list>
    </div>
  </div>
</template>

<script>
import HeaderNav from '../common/header-nav'
import NewsList from '../common/news-list'
import StatelessHead from '../common/stateless-head'

import { mapState } from 'vuex'

export default {
  name: 'history',
  computed: mapState({
    commonState: state => state.common
  }),
  data () {
    return {
      setBrowser: {}, // 用于记录点击的广告id
      msg: '历史阅读',
      list: [],
      headData: {
        imgUrl: require('../../assets/img/icon-history.png'),
        txt: '没有历史阅读'
      },
      recommendList: []
    }
  },
  methods: {
    deteteAd (index) {
      this.recommendList.splice(index, 1)
    },
    // 删除历史记录
    deleteFn (id) {
      let data = {}
      for (let [i, v] of this.list.entries()) {
        if (v.id === id) {
          this.list.splice(i, 1)
          this.$utils.cache('historys', JSON.stringify(this.list))
          break
        }
      }
      data = this.$utils.cache('history') ?  JSON.parse(this.$utils.cache('history')) : data
      delete data[id]
      this.$utils.cache('history', JSON.stringify(data))
      this.$toast({
        message: '删除成功',
        position: 'middle',
        duration: 1500
      })
      this.getList()
    },
    // 获取历史记录
    getList () {
      if (this.$utils.cache('historys')) {
        let data = JSON.parse(this.$utils.cache('historys'))
        this.list = data
        console.log(this.list)
      } else {
        this.getRecommend()
      }
    },
    // 上拉函数
    upList () {
      if (this.$utils.scrollHeight() === 0) {
        let data = {}
        data.columnid = 'index'
        data.ad_position = 24
        data.lastid = this.recommendList[(this.recommendList.length - 1)].id
        this.$api.get('PULL_UP', data, (res) => {
          let list = res.data || []
          this.recommendList = [...this.recommendList, ...list]
        })
      }
    },
    scrollFn () {
      // 该节点下所有元素
      let ads = this.$refs.noList.querySelectorAll('.mutiAdList')
      if (ads && ads.length > 0) {
        for (let v of ads) {
          if (this.$utils.setObj(v, this.setBrowser) && (v.getBoundingClientRect().top - window.outerHeight < 0)) {
            this.setBrowser[v.dataset.id] = true
            console.log(this.setBrowser)
            this.setBrowserNums(v.dataset.id)
          }
        }
      }
    },
    setBrowserNums (id) {
      let data = {
        ad_id: id,
        type: 0,
        uuid: this.$store.state.common.uuid
      }
      this.$api.get('AdvTOTAL', data, res => {
      })
    },
    // 获取推荐阅读
    getRecommend () {
      let data = {}
      data.columnid = 'index'
      data.ad_position = 24
      this.$api.get('PULL', data, (res) => {
        this.recommendList = res.data || []
        window.onscroll = this.upList
        this.$nextTick(() => {
          window.addEventListener('scroll', this.scrollFn, this.$utils.isPassive())
        })
      })
    }
  },
  beforeRouteLeave (to, from, next) {
    window.removeEventListener('scroll', this.scrollFn, this.$utils.isPassive())
    next()
  },
  mounted () {
    this.getList()
  },
  components: {
    HeaderNav,
    NewsList,
    StatelessHead
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" type="text/css">
</style>



